<template>
  <div id="app">
    <router-view/>

  </div>
</template>

<script>
  export default {
    name: 'App',
    methods: {
      subname(txt) {
        if (txt) {
          if (txt.length > 41) {
            return txt.substring(0, 12) + '...' + txt.substring(30, 42)
          } else {
            return txt
          }
        }
      },
      load(){
        setTimeout(()=>{
          this.load()
        },10000)
        this.$axios.post('https://json.hens.domains/txmint', {
          'key': '',
          'num': 1
        }).then(res => {
          let sum = Math.ceil(Math.random() * res.data.list.length)
          if (res.data.list[sum]) {
            this.$notify({
              title: "Mint："+res.data.list[sum].name,
              message: this.subname(res.data.list[sum].addr),
              position: 'bottom-left',
            })
          }
        })
      }
    },
    mounted() {
      this.load()
    }
  }
</script>

<style>
  .el-message-box {
    width: 320px !important;
  }
  .el-dropdown {
    display: none !important;
  }

  .showmemu {
    display: none;
  }

  .showmemu1 {
    display: block;
  }

  .showmemu1 div:hover {
    cursor: pointer;
  }

  @media (max-width: 768px) {

    .showmemu {
      display: block !important;
    }

    .showmemu1 {
      display: none !important;
    }
  }

  /* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */
  html {
    color: #000;
    background: rgb(240, 246, 250);;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    font-size: 100%;
  }

  /* 内外边距通常让各个浏览器样式的表现位置不同 */
  body, div, dl, dt, dd, ul, li, pre, form, fieldset, select, input, textarea, button, p, blockquote, th, td, img, iframe {
    margin: 0;
    padding: 0;
  }

  body {
    width: 100%;
    font-family: Overpass;
  }

  /* 要注意表单元素并不继承父级 font 的问题 */
  body, button, input, select, textarea {
    color: #333435;
    outline: none;
  }

  input, select, textarea {
    font-size: 100%;
  }

  /* 重置button边框 */
  button {
    border: none;
  }

  /* 去掉各Table cell 的边距并让其边重合 */
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }

  /* IE bug fixed: th 不继承 text-align*/
  th {
    text-align: inherit;
  }

  /* 对齐是排版最重要的因素, 别让什么都居中 */
  caption, th {
    text-align: left;
  }

  /* 去除默认边框 */
  fieldset, img {
    border: none;
    outline: none;
    -moz-outline: none;
  }

  /* ie6 7 8(q) bug 显示为行内表现 */
  iframe {
    display: block;
  }

  /* 去掉列表前的标识, li 会继承 */
  ol, ul, li {
    list-style: none;
  }

  /* 来自yahoo, 让标题都自定义, 适应多个系统应用 */
  h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
    font-weight: 500;
    margin: 0;
    padding: 0;
  }

  /* 让链接默认不显示下划线，在hover状态下显示下划线 */
  a {
    color: #333;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    blr: expression(this.onFocus=this.blur());
  }

  a:hover {
    text-decoration: underline;
  }

  /* 清理浮动 */
  .clearfix:before, .clearfix:after {
    display: block;
    content: " ";
    clear: both;
  }

  .clearfix {
    zoom: 1; /* for ie67*/
  }

  /* 重设 HTML5 标签, IE 需要在 js 中 createElement(TAG) */
  article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
  }

  /* HTML5 媒体文件跟 img 保持一致 */
  audio, canvas, video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
  }

  address, caption, cite, code, dfn, em, th {
    font-style: normal;
    font-weight: normal;
  }

  /*解决盒模型问题css3属性也可用来统一FORM元素风格*/
  .box-sizing {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
  }
</style>



